@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.skeleton {
  -webkit-animation: skeleton-loading 1s linear infinite alternate;
          animation: skeleton-loading 1s linear infinite alternate;
}

@-webkit-keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 70%);
  }
  100% {
    background-color: hsl(200, 20%, 95%);
  }
}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 70%);
  }
  100% {
    background-color: hsl(200, 20%, 95%);
  }
}

.skeleton.image {
  height: 250px;
  width: 250px;
  /* background-size: cover; */
  margin-bottom: 20px;
}

.skeleton.title {
    width: 50%;
    height: 28px;
    margin-bottom: 15px;
}

.skeleton.para {
    width: 90%;
    height: 12px;
    margin: 16px;
}

.skeleton-card {
  box-shadow: 2px 2px 4px grey;
  margin: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 250px;
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.App {
  min-height: 100vh;
  /* background-color: #282a36; */
}

header {
  position: fixed;
  right: 0;
  left: 0;
  box-shadow: 0px 0px 2px gray;
  background-color: white;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.search-form {
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.search-bar {
  width: 30vw;
  height: 21px;
  font-size: large;
  border: none;
  padding: 10px;
  border-radius: 5px 0px 0px 5px;
  background-color: #eeeeee;
  outline: none;
  border: 2px solid #eeeeee;
  color: #44475a;;
}

.search-bar:focus {
  background-color: white;
  border: 2px solid #50fa7b;
}

.search-btn {
  background: #50fa7b;
  height: 45px;
  border: none;
  padding: 10px 20px;
  color: #44475a;
  border-radius: 0px 5px 5px 0px;
  cursor: pointer;
}

.recipes {
  padding-top: 75px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.fillerDivs {
  content: ' ';
  width: 290px;
  /* margin: 20px; */
}

h1 {
  color: #44475a;
  font-family: 'Lobster', cursive;
  font-size: 40px;
  margin: 0;
  margin-left: 5vw;
  cursor: default;
}

.nav-list {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  margin: 0;
  height: 100%;
}

.nav-list li{
  padding-left: 40px;
  padding-right: 40px;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 50px;
  font-size: large;
  font-family: 'Lobster', cursive;
}

.nav-list li:hover {
  background-color: #50fa7b;
  cursor: pointer;
}



.recipe {
  box-shadow: 2px 2px 4px grey;
  margin: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 250px;
}

.recipe:hover {
  box-shadow: 0px 0px 20px grey;
}

.images {
  height: 250px;
  width: 250px;
  /* width: 100%; */
  background-size: cover;
  margin-bottom: 20px;
  /* border-radius: 10px; */
}

.title {
  margin: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.navigation {
  height: 100%;
  /* position: relative; */
}

.popular-dropdown {
  padding: 0;
  margin: 0;
  border: 1px solid #44475a;
  background-color: white;
  display: none;
  top: 70px;
  left: 0px;
  position: absolute;
  z-index: 1;
  list-style: none;
}

.popular-dropdown li {
  padding: 20px;
  padding-left: 60px;
  padding-right: 60px;
}

.popular-dropdown li:hover {
  background-color: #50fa7b;
}

.popular {
  position: relative;
}

.popular:hover .popular-dropdown {
  display: block;
}
